<template>
  <div>
    <Modal v-model="showModal" :footerHide="true" :title="modalTitle" style="text-align: center" width="700px">
      <Form :model="item" :label-width="80">
        <FormItem label="活动ID" style="width: 400px">
          <Input v-model="item.activityId" type="text" placeholder="请输入活动ID" />
        </FormItem>
        <Form-item label="活动图片">
          <Row>
            <Col span="3">
              <Upload action="/api/admin/file/upload" :show-upload-list="false"
                      :on-success="handleSuccess"
                      :on-error="handleErr"
                      :before-upload="beforeUpload"
                      :on-format-error="handleFormatError"
                      :format="['jpg','jpeg','png']">
                <Button type="ghost" icon="ios-cloud-upload-outline">上传</Button>
                <div style="line-height: 20px; margin-top: 5px">（请上传小于500kb的图片）</div>
              </Upload>
            </Col>
            <Col span="6" style="margin-left: 50px">
              <div class="image-upload">
                <img :src="item.cover" style="max-height: 240px">
              </div>
            </Col>
          </Row>
        </Form-item>
        <Button type="primary" style="width: 120px; margin-bottom: 24px" @click="submit()">提交</Button>
      </Form>
    </Modal>
  </div>
</template>

<script>
  import qs from 'qs'
  export default {
    data () {
      return {
        showModal: false,
        modalTitle: '',
        item: {
          id: '',
          activityId: '',
          cover: ''
        }
      }
    },
    methods: {
      clearItem () {
        this.item.id = ''
        this.item.activityId = ''
        this.item.cover = ''
      },
      show () {
        this.modalTitle = '新增图片'
        this.clearItem()
        this.showModal = true
      },
      showDetail (item) {
        this.modalTitle = '编辑图片'
        this.item.id = item.Id
        this.item.activityId = item.ActivityId
        this.item.cover = item.Cover
        this.showModal = true
      },
      handleSuccess (res) {
        if (res.status === 10000) {
          this.item.cover = res.filepath
          this.$Message.success('上传成功')
        } else {
          this.$Message.error('上传失败,请重试')
        }
      },
      handleErr () {
        this.$Message.error('通讯错误，请重试')
      },
      beforeUpload () {
      },
      handleFormatError () {
        this.$Message.error('文件格式不正确,请上传 jpg 或 png 格式的图片')
      },
      submit () {
        console.log(this.item)
        if (!this.item.activityId || !this.item.cover) {
          this.$Message.error('内容填写不完整')
          return
        }
        this.$http.post('/api/admin/swiper/save', qs.stringify(this.item)).then((response) => {
          if (response.data.status === 10000) {
            this.$Message.success('操作成功')
            this.$loadingHide()
          } else {
            this.$loadingHide()
            this.$Message.error('获取失败，请重试')
          }
          this.$emit('refresh')
          this.showModal = false
          this.clearItem()
        }).catch((err) => {
          console.log(err)
          this.$emit('refresh')
          this.showModal = false
          this.clearItem()
          this.$loadingHide()
          this.$Message.error('通讯失败，请重试')
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>

</style>



