import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import plane from '@/components/main'
import activity from '@/components/activity/main'
import user from '@/components/user/main'
import audit from '@/components/swiper/main'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      name: 'login',
      component: login
    },
    {
      path: '/admin/main',
      name: 'main',
      component: plane,
      children: [
        {
          path: '/admin/main/activity',
          component: activity
        },
        {
          path: '/admin/main/user',
          component: user
        },
        {
          path: '/admin/main/swiper',
          component: audit
        }
      ]
    }
  ]
})




