<template>
  <div>
    <Modal v-model="showModal" :footerHide="true" :title="modalTitle" style="text-align: center" width="700px">
      <Form :model="item" :label-width="80">
        <FormItem label="活动名称">
          <Input v-model="item.title" type="text" placeholder="请输入活动名称" />
        </FormItem>
        <FormItem label="活动简介">
          <Input v-model="item.introduction" :rows="4" type="textarea" placeholder="请输入活动简介" />
        </FormItem>
        <FormItem label="限制人数" style="text-align: left">
          <Input v-model="item.userLimit" style="width: 200px" type="text" placeholder="请输入活动限制人数" />
        </FormItem>
        <FormItem label="活动状态" style="text-align: left">
          <Select v-model="item.status" style="width:200px">
            <Option v-for="item in choiceStatus" :value="item.value" :key="item.value">{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem label="活动日期" style="text-align: left !important;">
          <DatePicker id="datePicker" @on-change="handleDateChange" @on-clear="handleDateClear" type="daterange" placement="right-start" placeholder="请选择活动日期" style="width: 200px"></DatePicker>
        </FormItem>
        <Form-item label="活动图片">
          <Row>
            <Col span="3">
              <Upload action="/api/admin/file/upload" :show-upload-list="false"
                      :on-success="handleSuccess"
                      :on-error="handleErr"
                      :before-upload="beforeUpload"
                      :on-format-error="handleFormatError"
                      :format="['jpg','jpeg','png']">
                <Button type="ghost" icon="ios-cloud-upload-outline">上传</Button>
                <div style="line-height: 20px; margin-top: 5px">（请上传小于500kb的图片）</div>
              </Upload>
            </Col>
            <Col span="6" style="margin-left: 50px">
              <div class="image-upload">
                <div v-for="img in imgList">
                  <img :src="img" style="max-height: 240px">
                </div>
              </div>
            </Col>
          </Row>
        </Form-item>
        <Button type="primary" style="width: 120px; margin-bottom: 24px" @click="submit()">提交</Button>
      </Form>
    </Modal>
  </div>
</template>

<script>
  import qs from 'qs'
  export default {
    data () {
      return {
        showModal: false,
        modalTitle: '',
        item: {
          id: '',
          title: '',
          introduction: '',
          startTime: '',
          endTime: '',
          imgUrl: '',
          cover: '',
          userLimit: '',
          status: 1
        },
        imgList: [],
        professions: [],
        choiceStatus: [
          {
            value: 1,
            label: '可报名'
          },
          {
            value: 0,
            label: '不可报名'
          }
        ]
      }
    },
    methods: {
      clearItem () {
        this.item.id = ''
        this.item.title = ''
        this.item.introduction = ''
        this.item.startTime = ''
        this.item.endTime = ''
        this.item.imgUrl = ''
        this.item.cover = ''
        this.item.userLimit = ''
        this.item.status = 1
        this.imgList = []
      },
      show () {
        this.modalTitle = '新增活动'
        document.getElementById('datePicker').getElementsByTagName("input")[0].value=""
        this.clearItem()
        this.showModal = true
      },
      showDetail (item) {
        this.modalTitle = '编辑活动'
        this.item.id = item.Id
        this.item.title = item.Title
        this.item.introduction = item.Introduction
        this.item.startTime = item.StartTime
        this.item.endTime = item.EndTime
        this.item.imgUrl = item.ImgUrl
        this.imgList = item.ImgUrl.split(',')
        this.item.userLimit = item.UserLimit
        this.item.status = item.Status
        this.showModal = true
      },
      handleSuccess (res) {
        if (res.status === 10000) {
          this.imgList.push(res.filepath)
          this.$Message.success('上传成功')
        } else {
          this.$Message.error('上传失败,请重试')
        }
      },
      handleErr () {
        this.$Message.error('通讯错误，请重试')
      },
      beforeUpload () {
      },
      handleFormatError () {
        this.$Message.error('文件格式不正确,请上传 jpg 或 png 格式的图片')
      },
      submit () {
        this.item.imgUrl = ''
        if (!this.item.title || !this.item.introduction || !this.item.startTime || !this.imgList) {
          this.$Message.error('内容填写不完整')
          return
        }
        this.imgList.forEach((item) => {
          this.item.imgUrl += item + ','
        })
        this.item.imgUrl = this.item.imgUrl.substring(0,this.item.imgUrl.length - 1)
        this.item.cover = this.imgList[0]
        this.$http.post('/api/admin/activity/save', qs.stringify(this.item)).then((response) => {
          if (response.data.status === 10000) {
            this.$Message.success('操作成功')
            this.$loadingHide()
          } else {
            this.$loadingHide()
            this.$Message.error('获取失败，请重试')
          }
          this.$emit('refresh')
          this.showModal = false
          this.clearItem()
        }).catch((err) => {
          console.log(err)
          this.$emit('refresh')
          this.showModal = false
          this.clearItem()
          this.$loadingHide()
          this.$Message.error('通讯失败，请重试')
        })
      },
      handleDateChange (date) {
        this.item.startTime = date[0] + " 00:00:00"
        this.item.endTime = date[1] + " 00:00:00"
      },
      handleDateClear () {
        this.item.startTime = ''
        this.item.endTime = ''
      }
    }
  }
</script>

<style lang="stylus" scoped>

</style>



