<style lang="stylus">
  body {
    font-family : 'Open Sans', sans-serif;
  }

  .mikepad-loading {
    width : 200px;
    position: absolute;
    top: 20%;
    left : 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
    -moz-transform: translateY(-50%) translateX(-50%);
    -o-transform: translateY(-50%) translateX(-50%);
    transform: translateY(-50%) translateX(-50%);
  }

  .mikepad-loading .binding {
    content : '';
    width : 27px;
    height : 4px;
    border : 2px solid #57a3f3
    margin : 0 auto;
  }

  .mikepad-loading .pad {
    width : 15px;
    height : 18px;
    border : 2px solid #57a3f3
    border-top : 0;
    padding : 6px;
    margin : 0 auto;
  }

  .mikepad-loading .line {
    width : 15px;
    margin-top : 4px;
    border-top : 2px solid #57a3f3
    opacity : 0;
    -webkit-animation : writeline 3s infinite ease-in;
    -moz-animation : writeline 3s infinite ease-in;
    -o-animation : writeline 3s infinite ease-in;
    animation : writeline 3s infinite ease-in;
  }

  .mikepad-loading .line:first-child {
    margin-top : 0;
  }

  .mikepad-loading .line.line1 {
    -webkit-animation-delay: 0s;
    -moz-animation-delay: 0s;
    -o-animation-delay: 0s;
    animation-delay: 0s;
  }

  .mikepad-loading .line.line2 {
    -webkit-animation-delay: 0.5s;
    -moz-animation-delay: 0.5s;
    -o-animation-delay: 0.5s;
    animation-delay: 0.5s;
  }

  .mikepad-loading .line.line3 {
    -webkit-animation-delay: 1s;
    -moz-animation-delay: 1s;
    -o-animation-delay: 1s;
    animation-delay : 1s;
  }

  .mikepad-loading .text {
    text-align : center;
    margin-top : 10px;
    font-size : 15px;
    color : #57a3f3;
    font-weight 600
  }

  @-webkit-keyframes writeline {
    0% { width : 0px; opacity: 0; }
    33% { width : 15px; opacity : 1; }
    70% { opacity : 1; }
    100% {opacity : 0; }
  }

  @-moz-keyframes writeline {
    0% { width : 0px; opacity: 0; }
    33% { width : 15px; opacity : 1; }
    70% { opacity : 1; }
    100% {opacity : 0; }
  }

  @-o-keyframes writeline {
    0% { width : 0px; opacity: 0; }
    33% { width : 15px; opacity : 1; }
    70% { opacity : 1; }
    100% {opacity : 0; }
  }

  @keyframes writeline {
    0% { width : 0px; opacity: 0; }
    33% { width : 15px; opacity : 1; }
    70% { opacity : 1; }
    100% {opacity : 0; }
  }

  .loadingWrapper
    width 100%
    height 100%
    background rgba(0, 0, 0, 0.1)
    position fixed
    top 0
    left 0
    z-index 999
</style>

<template>
  <div class="loadingWrapper" v-show="visible">
    <div class="mikepad-loading">
      <div class="binding"></div>
      <div class="pad">
        <div class="line line1"></div>
        <div class="line line2"></div>
        <div class="line line3"></div>
      </div>
      <div class="text">
        加载中...
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visible: false
      }
    },
    methods: {
      show() {
        this.visible = true
      },
      hide() {
        console.log('close')
        this.visible = false
      }

    }
  }
</script>




