<template>
  <div>
    <Table stripe :columns="columns" :data="data"></Table>
    <div class="add">
      <Button type="info" icon="plus" @click="$refs.edit.show()">新增活动</Button>
    </div>
    <div class="pagesWrapper">
      <Page :total="count" show-total @on-change="pageChange" :current="query.page"></Page>
    </div>
    <Modal v-model="removeWrapper" :footerHide="true" title="删除活动" style="text-align: center">
      <div style="font-size: 15px">
        确认删除此活动？
      </div>
      <Button type="primary" @click="remove" style="margin-top: 20px">确定</Button>
    </Modal>
    <edit ref="edit" @refresh="reList()"></edit>
    <audit ref="audit" @refresh="reList()"></audit>
  </div>
</template>

<script>
  import qs from 'qs'
  import edit from './edit.vue'
  import audit from './audit.vue'
  export default {
    components: {
      edit,
      audit
    },
    data () {
      return {
        columns: [
          {
            title: 'ID',
            key: 'Id'
          },
          {
            title: '活动名称',
            key: 'Title'
          },
          {
            title: '创建时间',
            render: (h, params) => {
              let time = params.row.CreatedTime.replace('T', ' ').substring(0, 16)
              return h('span', time)
            }
          },
          {
            title: '活动日期',
            render: (h, params) => {
              let time = params.row.StartTime.substring(0, 10) + ' ~ ' + params.row.EndTime.substring(0, 10)
              return h('span', time)
            }
          },
          {
            title: '操作',
            width: 300,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.$refs.audit.show(params.row.Id)
                    }
                  }
                }, '报名审核'),
                h('Button', {
                  props: {
                    type: 'warning',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.$refs.edit.showDetail(params.row)
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.removeShow(params.row.Id)
                    }
                  }
                }, '删除')
              ])
            }
          }
        ],
        data: [
          {
            "Id": 1,
            "Title": "123",
            "Introduction": "123",
            "ImgUrl": "1,2,3",
            "UserCount": 0,
            "UserLimit": 10,
            "Status": 0,
            "StartTime": "2018-05-07 00:00:00",
            "EndTime": "2018-05-18 00:00:00",
            "CreatedTime": "2018-05-16T10:40:03+08:00",
            "UpdatedTime": "2018-05-16T10:40:03+08:00"
          }
        ],
        count: 0,
        query: {
          page: 1,
          per: 10
        },
        removeWrapper: false,
        removeID: 0
      }
    },
    created () {
      this.reList()
    },
    methods: {
      getList () {
        this.$http.post('/api/admin/activity/list', qs.stringify(this.query)).then((response) => {
          this.data = response.data.data
          this.count = response.data.total
          this.query.page = response.data.page
          this.query.per = response.data.per
          this.$loadingHide()
        }).catch(() => {
          this.$loadingHide()
          this.$Message.error('通讯失败，请重试')
        })
      },
      reList () {
        this.$loading()
        this.query.page = 1
        this.query.per = 10
        this.getList()
      },
      pageChange (page) {
        this.$loading()
        this.query.page = page
        this.getList()
      },
      removeShow (id) {
        this.removeWrapper = true
        this.removeID = id
      },
      remove () {
        this.$loading()
        this.$http.post('/api/admin/activity/delete?id=' + this.removeID).then((response) => {
          if (response.data.status === 10000) {
            this.getList()
            this.removeWrapper = false
            this.removeID = 0
            this.$Message.success('删除成功')
          } else {
            this.$loadingHide()
            this.$Message.error('提交失败，请重试')
            this.reList()
          }
        }).catch(() => {
          this.$loadingHide()
          this.removeWrapper = false
          this.$Message.error('通讯失败，请重试')
          this.reList()
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .pagesWrapper
    margin-top 20px
    text-align right
  .add
    margin-top: 20px
</style>



