<template>
  <div>
    <Table stripe :columns="columns" :data="data"></Table>
    <div class="pagesWrapper">
      <Page :total="count" show-total @on-change="pageChange" :current="query.page"></Page>
    </div>
    <Modal v-model="removeWrapper" :footerHide="true" :title="modalTitle" style="text-align: center">
      <div style="font-size: 15px">
        {{ modalContent }}
      </div>
      <Button type="primary" @click="remove" style="margin-top: 20px">确定</Button>
    </Modal>
    <edit ref="edit" @refresh="reList()"></edit>
    <audit ref="audit" @refresh="reList()"></audit>
  </div>
</template>

<script>
  import qs from 'qs'
  export default {
    data () {
      return {
        modalTitle: '',
        modalContent: '',
        columns: [
          {
            title: 'ID',
            key: 'Id'
          },
          {
            title: '用户名',
            key: 'Name'
          },
          {
            title: '创建时间',
            width: 200,
            render: (h, params) => {
              let time = params.row.CreatedTime.replace('T', ' ').substring(0, 16)
              return h('span', time)
            }
          },
          {
            title: '状态',
            render: (h, params) => {
              let status = ''
              console.log(params.row.CanSignUp)
              if (params.row.CanSignUp == 1) {
                status = '正常'
              } else {
                status = '禁止报名'
              }
              return h('span', status)
            }
          },
          {
            title: '操作',
            width: 200,
            render: (h, params) => {
              if (params.row.CanSignUp == 1) {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    on: {
                      click: () => {
                        this.removeShow1(params.row.Id)
                      }
                    }
                  }, '禁止报名')
                ])
              } else {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'success',
                      size: 'small'
                    },
                    on: {
                      click: () => {
                        this.removeShow2(params.row.Id)
                      }
                    }
                  }, '解除禁止')
                ])
              }
            }
          }
        ],
        data: [
          {
            "Id": 1,
            "Name": "qwe",
            "CanSignUp": 1,
            "CreatedTime": "2018-05-16T10:40:03+08:00",
            "UpdatedTime": "2018-05-16T10:40:03+08:00"
          },
          {
            "Id": 0,
            "Name": "123",
            "CanSignUp": 0,
            "CreatedTime": "2018-05-16T10:40:03+08:00",
            "UpdatedTime": "2018-05-16T10:40:03+08:00"
          }
        ],
        count: 0,
        query: {
          page: 1,
          per: 10
        },
        removeWrapper: false,
        removeID: 0,
        status: ''
      }
    },
    created () {
      this.reList()
    },
    methods: {
      getList () {
        this.$http.post('/api/admin/user/list', qs.stringify(this.query)).then((response) => {
          this.data = response.data.data
          this.count = response.data.total
          this.query.page = response.data.page
          this.query.per = response.data.per
          this.$loadingHide()
        }).catch(() => {
          this.$loadingHide()
          this.$Message.error('通讯失败，请重试')
        })
      },
      reList () {
        this.$loading()
        this.query.page = 1
        this.query.per = 10
        this.getList()
      },
      pageChange (page) {
        this.$loading()
        this.query.page = page
        this.getList()
      },
      removeShow1 (id) {
        this.modalTitle = '禁止操作'
        this.modalContent = '确认禁止该用户报名？'
        this.removeWrapper = true
        this.removeID = id
        this.status = 0
      },
      removeShow2 (id) {
        this.modalTitle = '解除禁止操作'
        this.modalContent = '确认解除禁止该用户报名？'
        this.removeWrapper = true
        this.removeID = id
        this.status = 1
      },
      remove () {
        this.$loading()
        this.$http.post('/api/admin/user/handle?id=' + this.removeID + '&status=' + this.status).then((response) => {
          if (response.data.status === 10000) {
            this.getList()
            this.removeWrapper = false
            this.removeID = 0
            this.$Message.success('操作成功')
          } else {
            this.$loadingHide()
            this.$Message.error('提交失败，请重试')
            this.reList()
          }
        }).catch(() => {
          this.$loadingHide()
          this.removeWrapper = false
          this.$Message.error('通讯失败，请重试')
          this.reList()
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .pagesWrapper
    margin-top 20px
    text-align right
  .add
    margin-top: 20px
</style>



