<style lang="stylus">
  #log
    .login
      display flex
      position fixed
      justify-content center
      flex-direction column
      align-items center
      height 100%
      width 100%
      background-size cover
      background #e7e7e7

    .login .header
      margin-top -100px
      margin-bottom 20px

    .login .logo
      width 240px

    .login .form
      display flex
      flex-direction column

    .login .form .username

      .ivu-input-large
        border 1px solid #7e7e7e
        border-radius 10px 10px 0 0
        border-bottom 0
        height 50px

      .ivu-icon
        margin-right 5px
        margin-top 8px
        font-size 30px

    .login .form .password
      .ivu-input-large
        border 1px solid #7e7e7e
        height 50px
        border-radius 0 0 10px 10px

      .ivu-icon
        margin-right 5px
        margin-top 8px
        font-size 30px

    .login .button
      margin-top 30px

</style>

<template>
  <div id="log">
    <div class="login">
      <div class="header">
        报名系统管理后台
      </div>
      <div class="form">
        <Input class="username" v-model="form.Username" icon="ios-person-outline" placeholder="请输入账号"
               style="width: 300px" size="large"/>
        <Input class="password" v-model="form.Password" icon="ios-locked-outline" type="password" placeholder="请输入密码"
               style="width: 300px" size="large"/>
      </div>
      <div class="button">
        <Button type="primary" @click="login" :loading="isLoading" style="width: 300px;height:50px;font-size: 16px">登录
        </Button>
      </div>
    </div>
  </div>

</template>

<script>
  import formCheck from '../utils/form_check'
  import qs from 'qs'

  export default {
    data() {
      return {
        isLoading: false,
        username: '',
        form: {
          Username: '',
          Password: ''
        }
      }
    },
    methods: {
      login() {
        console.log(this.form)
        if (!formCheck(this.form)) {
          this.$Message.error('内容填写不完整')
          return
        }
        this.isLoading = true
        this.$http.post('/api/admin/login', qs.stringify(this.form)).then((response) => {
          this.isLoading = false
          if (response.data.status === 10000) {
            this.$Message.success('登录成功')
            this.$router.push({path: '/admin/main/activity'})
          } else {
            this.$Message.error('登录错误，请稍候再试')
          }
        }).catch(() => {
          this.isLoading = false
          this.$Message.error('通讯失败，请重试')
        })
      }
    }
  }
</script>




